import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Grid, Row, Col, DropdownButton, MenuItem, Button, FormGroup, FieldGroup, FormControl, ControlLabel } from 'react-bootstrap';
import * as actions from '../../actions/orderFormActions';
import ReactHighcharts from 'react-highcharts'; // Expects that Highcharts was loaded in the code.
import HighchartsMore from 'highcharts-more';
HighchartsMore(ReactHighcharts.Highcharts);

const config = {
    chart: {
          type: 'bubble',
          plotBorderWidth: 1,
      },
      legend: {
          enabled: false
      },

      title: {
          text: 'Volume of exchange'
      },

      xAxis: {
          gridLineWidth: 1,
          labels: {
              format: '{value} K BTC'
          },
          plotLines: [{
              color: 'black',
              dashStyle: 'dot',
              width: 2,
              value: 25,
              zIndex: 3
          }]
      },

      yAxis: {
          startOnTick: false,
          endOnTick: false,
          labels: {
              format: '{value} K BTC'
          },
          plotLines: [{
              color: 'black',
              dashStyle: 'dot',
              width: 2,
              value: 25,
              zIndex: 3
          }]
      },

      tooltip: {
          useHTML: true,
          headerFormat: '<table>',
          pointFormat: '<tr><th colspan="2"><h3>{point.name}</h3></th></tr>' +
              '<tr><th>BTC Volume:</th><td>{point.x}</td></tr>',
          footerFormat: '</table>',
          followPointer: true
      },

      plotOptions: {
          series: {
              dataLabels: {
                  enabled: true,
                  format: '{point.name}'
              }
          }
      },

      series: [{
          data: [
              { x: 5, y: 5, z: 5, name: 'GDAX'},
              { x: 11, y: 11, z: 11, name: 'Bitfinex'},
              { x: 8, y: 8, z: 8, name: 'Binance'},
              { x: 21, y: 21, z: 21, name: 'CEX.IO'},
              { x: 13, y: 13, z:13, name: 'Bittrex'},
              { x: 7.1, y: 7.1, z: 7.1, name: 'Poloniex'},
              { x: 9, y: 9, z: 9, name: 'HitBTC'},
              { x: 18, y: 18, z: 18, name: 'Krakan'}
          ]
      }]
};


export class PriceChart extends React.Component {

    state = {
        disableSubmit : false
    }

    handleTabClick = (event) => {
        this.props.actions.updateOrderFormTab( event.target.getAttribute('data-tab') );
    };

    handleBuySellToggle = () => {
        this.props.actions.toggleBuySellOrderForm();
    };

    handleExchangeSelection = (value) => {
        this.props.actions.updateOrderFormExchange( value );
    };

    handleAmountChange = (event) => {
        let parsedValue = parseFloat(event.target.value);
        this.setState({ disableSubmit : false })
        if( isNaN(parsedValue)  ) {
            parsedValue = 0;
        }

        let orderDetails = {
          buyAmount : parsedValue,
        };

        if( this.props.orderForm.activeTab === 'market' ) {
            let buySize;
            if( this.props.orderForm.isBuy ) {
                buySize = parsedValue / this.props.currentPair.lastTradePrice;
            } else {
                buySize = parsedValue * this.props.currentPair.lastTradePrice;
            }
            orderDetails.buySize = buySize;
        }

        this.props.actions.updateOrderForm( orderDetails );
    };

    placeOrder = () => {
        if( !this.props.orderForm.buyAmount ) {
            this.setState({ disableSubmit : true })
            return;
        }

        let now = new Date();
        let time = now.getHours() + ":" +  now.getMinutes() + ":" + now.getSeconds()
        let order = {
            size: this.props.orderForm.buySize,
            filled: "Executed",
            price: this.props.orderForm.buyAmount,
            time: time,
            status: "Executed",
            side: "Buy",
            exchange: this.props.orderForm.exchange
        }
        this.props.actions.placeOrder( order );
    };



  render() {
		return (
		  	<div className="card card-fh card-order-form">
		  		<div className="card-fixed-header">
					<h2 className="card-title" data-card="orderform" >Price Chart</h2>
				</div>
				<div className="card-panel-body p-15">
					<div className="tabs chart"  >
                        <ReactHighcharts config={config} ref="chart"></ReactHighcharts>
					</div>
				</div>
			</div>
		);
  }
}

function mapStateToProps(state) {
  return {
	currentPair: state.currentPair,
	orderForm : state.orderForm,
	appData : state.appData
  };
}

function mapDispatchToProps(dispatch) {
  return {
	actions: bindActionCreators(actions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PriceChart);



